@import url("https://fonts.googleapis.com/css2?family=Noto+Sans&display=swap");

:root {
  /* Color */
  --white-color: #fff;
  --black-color: #000;
  --light-mint-color: #c7e9e1;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Noto Sans";
  background-color: var(--light-mint-color);
}

html,
body,
#root,
.container {
  height: 100%;
  display: flex;
  justify-content: center;
}

.container .loader {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 300;
  font-size: 22px;
}